import { takeLatest } from "redux-saga/effects";
import axios from "axios";

function* fetchCustomerSaga() {

  yield axios({
    method: 'GET',
    url: '/api/customer',
  });
  // yield put({
  //   type: "SET_CUSTOMER",
  //   payload: response.data,
  // });
}

// function* addCustomerSaga(action) {
//   yield axios({
//     method: 'POST',
//     url: '/api/customer',
//     data: action.payload
//   });
// }

// Leads to nelify function
function* addCustomerSaga(action) {
  console.log('adding a customer, with action.payload', action.payload);

  yield fetch('/.netlify/functions/addCustomer', {
    body: JSON.stringify(action.payload),
    method: 'POST'
  }).then(response => {
    return response.json()
  }).catch((error) => {
    console.log('API error', error)
  })
}


function* customerSaga() {
  yield takeLatest('ADD_CUSTOMER', addCustomerSaga);
  yield takeLatest('FETCH_CUSTOMER', fetchCustomerSaga);
}

export default customerSaga;
